:root{
  --color-kfc:#483838;
  --beige-kfc:#42855B;
  --light-kfc:#90B77D;
  --white-kfc:#fff
}
.bg-color-body {
  background: var(--beige-kfc);
}
.bg-light-body {
  background: var(--white-kfc);
  border: none;
  box-shadow: 1px 1px 6px 2px rgba(0,0,0,0.1);
}
.color {
  color: var(--color-kfc);
}
.colorLight {
  color: var(--beige-kfc);
}
.outline-b{
  border: 1px solid var(--color-kfc);
  background: none;
  color: var(--color-kfc);
  transition: all .5s;
}
.outline-b:hover {
  background: var(--color-kfc);
  color: var(--white-kfc);
  border: none;
}
.underline {
  border: 2px solid var(--color-kfc);
  width: 150px;
  height: 1px;
  border-radius: 10px;
}
.font {
  font-family: JFont,'Cairo Play';
}
.brand {
  font-size: 40px;
  font-weight: 900;
}
.img-item {
  width: 200px;
  border-radius: 5px;
}
.price{
  color: #8b5456;
}
@font-face {
  font-family: JFont;
  src: url(../src/fonts/JF\ Flat\ regular.ttf);
}